const store = {};

/**
 * store
 * @param {string} key 
 * @param {[any,Function]} value 
 */
export const setStore = (key, value) => {
  if (key && typeof key === 'string') {
    store[key] = value;
  } else {
    console.error('a key is must be a tring for store');
  }
}

/**
 * store
 * @param {string} key 
 */
export const getStore = key => {
  if (key) {
    return store[key] || [];
  }
  return store;
}

export default getStore;
