import React from 'react';
import './index.less';

/**
 * 
 * @param {object} style 
 * @param {number} total 
 * @param {number} current 
*/
export default (props) => {
  const { total = 0, current = 0, style = {}} = props;
  const beRed = current > total;
  return (
    <span className={`words-count-down ${beRed ? 'warn' : ''}`} style={style}>{current}/{total}</span>
  );
}
